import { createContext, useState } from "react";

//Crear el context
export const ProductContext = createContext();

//Provider de datos y funciones
const ProductProvider = ({ children }) => {
  const [carrito, setCarrito] = useState([]);

  const addCarrito = (product) => {
    const copyCarrito = [...carrito];

    const productExistente = copyCarrito.find((l) => l._id === product._id);

    if (productExistente != null) {
      productExistente.count += 1;
    } else {
      copyCarrito.push({
        count: 1,
        product: product,
      });
    }
    setCarrito(copyCarrito);
  };

  return (
    <ProductContext.Provider
      value={{
        carrito,
        addCarrito,
      }}
    >
      {children}
    </ProductContext.Provider>
  );
};

export default ProductProvider;
