import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { useEffect, useState } from "react";
import { getProductByID } from "../services/products";
import Loading from "../components/loading/Loading";
import { useParams } from "react-router-dom";
import { Grid } from "@mui/material";
import { Container, Stack } from "@mui/system";

export default function Product() {
  const [isLoading, setIsLoading] = useState(false);
  const [product, setProduct] = useState({});
  const { id } = useParams();

  const getProductByIdAsync = async (id) => {
    setIsLoading(true);
    const data = await getProductByID(id);
    setProduct(data);
    setIsLoading(false);
  };

  useEffect(() => {
    getProductByIdAsync(id);
  }, [id]);

  const { name, image, description, countInStock } = product;

  if (isLoading) return <Loading />;

  return (
    <Container maxWidth="md" component="main">
      <Grid spacing={4}>
        <Card>
          <CardMedia
            component="img"
            sx={{ objectFit: "contain" }}
            image={`${process.env.REACT_APP_IMAGE_URL}/${image}`}
            title={name}
          />
          <CardContent>
            <Stack direction="column" spacing={1}>
              <Typography gutterBottom variant="h6" component="div">
                {name}
              </Typography>
              <Typography
                variant="body2"
                sx={{ fontStyle: "italic", textAlign: "justify" }}
                color="text.secondary"
              >
                {description}
              </Typography>
              <Typography variant="body2" color="text.primary">
                Stock: {countInStock}
              </Typography>
            </Stack>
          </CardContent>
          <CardActions>
            <Button size="small">Add item to cart</Button>
          </CardActions>
        </Card>
      </Grid>
    </Container>
  );
}
